"use client";

import { 
  FileText, 
  Calendar, 
  MessageCircle, 
  TrendingUp, 
  Users,
  CreditCard,
  CheckCircle,
  Clock,
  AlertCircle,
  Plus,
  ArrowUpRight,
  Zap
} from "lucide-react";
import React from "react";

const DashboardPage = () => {
  // Stats Data
  const stats = [
    {
      title: "Total Revenue",
      value: "AED 45,280",
      change: "+12.5%",
      trend: "up",
      icon: <TrendingUp size={20} className="text-[#2E69A4]" />,
      bgColor: "bg-blue-50"
    },
    {
      title: "Pending Invoices",
      value: "8",
      change: "-3.2%",
      trend: "down",
      icon: <FileText size={20} className="text-orange-500" />,
      bgColor: "bg-orange-50"
    },
    {
      title: "Active Clients",
      value: "23",
      change: "+8.3%",
      trend: "up",
      icon: <Users size={20} className="text-green-500" />,
      bgColor: "bg-green-50"
    },
    {
      title: "AI Conversations",
      value: "47",
      change: "+25.7%",
      trend: "up",
      icon: <MessageCircle size={20} className="text-purple-500" />,
      bgColor: "bg-purple-50"
    }
  ];

  // Quick Actions
  const quickActions = [
    {
      title: "Create Invoice",
      description: "Generate professional invoice",
      icon: <FileText size={24} className="text-[#2E69A4]" />,
      color: "bg-blue-50 hover:bg-blue-100"
    },
    {
      title: "Ask AI Assistant",
      description: "Get compliance help",
      icon: <MessageCircle size={24} className="text-[#2E69A4]" />,
      color: "bg-blue-50 hover:bg-blue-100"
    },
    {
      title: "Set Reminder",
      description: "Schedule deadlines",
      icon: <Calendar size={24} className="text-[#2E69A4]" />,
      color: "bg-blue-50 hover:bg-blue-100"
    },
    {
      title: "View Reports",
      description: "Business analytics",
      icon: <TrendingUp size={24} className="text-[#2E69A4]" />,
      color: "bg-blue-50 hover:bg-blue-100"
    }
  ];

  // Upcoming Deadlines
  const deadlines = [
    {
      title: "VAT Filing",
      date: "Due in 5 days",
      type: "urgent",
      completed: false
    },
    {
      title: "Trade License Renewal",
      date: "Due in 15 days",
      type: "warning",
      completed: false
    },
    {
      title: "Employee Payroll",
      date: "Due in 3 days",
      type: "urgent",
      completed: false
    },
    {
      title: "Quarterly Report",
      date: "Due in 30 days",
      type: "normal",
      completed: false
    }
  ];

  // Recent Activity
  const recentActivity = [
    {
      action: "Invoice Generated",
      client: "Alpha Technologies",
      amount: "AED 12,500",
      time: "2 hours ago",
      status: "completed"
    },
    {
      action: "AI Consultation",
      client: "Compliance Query",
      amount: "-",
      time: "4 hours ago",
      status: "completed"
    },
    {
      action: "Payment Received",
      client: "Beta Solutions",
      amount: "AED 8,750",
      time: "1 day ago",
      status: "completed"
    },
    {
      action: "Reminder Set",
      client: "VAT Filing",
      amount: "-",
      time: "2 days ago",
      status: "completed"
    }
  ];

  return (
    <div className="min-h-screen bg-gray-50 p-6">
      {/* Welcome Section */}
      <div className="mb-8">
        <h1 className="text-2xl font-bold text-gray-900">Good morning, Farhan</h1>
        <p className="text-gray-600 mt-1">Here's your business overview for today</p>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
        {stats.map((stat, index) => (
          <div key={index} className="bg-white rounded-lg border border-gray-200 p-6 hover:shadow-md transition-shadow">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">{stat.title}</p>
                <p className="text-2xl font-bold text-gray-900 mt-1">{stat.value}</p>
                <span className={`inline-flex items-center text-sm mt-1 ${
                  stat.trend === 'up' ? 'text-green-600' : 'text-red-600'
                }`}>
                  {stat.change} from last month
                </span>
              </div>
              <div className={`p-3 rounded-lg ${stat.bgColor}`}>
                {stat.icon}
              </div>
            </div>
          </div>
        ))}
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Left Column - Quick Actions & Recent Activity */}
        <div className="lg:col-span-2 space-y-6">
          {/* Quick Actions */}
          <div className="bg-white rounded-lg border border-gray-200">
            <div className="p-6 border-b border-gray-200">
              <h2 className="text-lg font-semibold text-gray-900">Quick Actions</h2>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4 p-6">
              {quickActions.map((action, index) => (
                <div
                  key={index}
                  className={`flex items-center gap-4 p-4 rounded-lg cursor-pointer transition-colors ${action.color} border border-gray-100`}
                >
                  <div className="p-2 bg-white rounded-lg border border-gray-200">
                    {action.icon}
                  </div>
                  <div>
                    <h3 className="font-medium text-gray-900">{action.title}</h3>
                    <p className="text-sm text-gray-600 mt-1">{action.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>

          {/* Recent Activity */}
          <div className="bg-white rounded-lg border border-gray-200">
            <div className="p-6 border-b border-gray-200 flex items-center justify-between">
              <div>
                <h2 className="text-lg font-semibold text-gray-900">Recent Activity</h2>
                <p className="text-sm text-gray-600 mt-1">Latest business actions</p>
              </div>
              <button className="text-sm text-[#2E69A4] hover:text-[#1b2a49] font-medium">
                View All
              </button>
            </div>
            <div className="divide-y divide-gray-100">
              {recentActivity.map((activity, index) => (
                <div key={index} className="p-4 hover:bg-gray-50 transition-colors">
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-3">
                      <div className="w-8 h-8 bg-blue-50 rounded-lg flex items-center justify-center">
                        <CheckCircle size={16} className="text-green-500" />
                      </div>
                      <div>
                        <p className="font-medium text-gray-900">{activity.action}</p>
                        <p className="text-sm text-gray-600">{activity.client}</p>
                      </div>
                    </div>
                    <div className="text-right">
                      <p className="font-medium text-gray-900">{activity.amount}</p>
                      <p className="text-sm text-gray-500">{activity.time}</p>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* Right Column - Upcoming Deadlines & AI Insights */}
        <div className="space-y-6">
          {/* Upcoming Deadlines */}
          <div className="bg-white rounded-lg border border-gray-200">
            <div className="p-6 border-b border-gray-200">
              <h2 className="text-lg font-semibold text-gray-900">Upcoming Deadlines</h2>
              <p className="text-sm text-gray-600 mt-1">Compliance and important dates</p>
            </div>
            <div className="p-4 space-y-3">
              {deadlines.map((deadline, index) => (
                <div
                  key={index}
                  className="flex items-center justify-between p-3 rounded-lg border border-gray-100 hover:bg-gray-50 transition-colors"
                >
                  <div className="flex items-center gap-3">
                    <div className={`w-2 h-2 rounded-full ${
                      deadline.type === 'urgent' ? 'bg-red-500' :
                      deadline.type === 'warning' ? 'bg-yellow-500' : 'bg-blue-500'
                    }`}></div>
                    <div>
                      <p className="font-medium text-gray-900 text-sm">{deadline.title}</p>
                      <p className="text-xs text-gray-600">{deadline.date}</p>
                    </div>
                  </div>
                  <button className="text-xs text-[#2E69A4] hover:text-[#1b2a49] font-medium">
                    Set Reminder
                  </button>
                </div>
              ))}
            </div>
          </div>

          {/* AI Business Insights */}
          <div className="bg-gradient-to-br from-[#1b2a49] to-[#2E69A4] rounded-lg text-white">
            <div className="p-6">
              <div className="flex items-center gap-2 mb-4">
                <Zap size={20} className="text-cyan-300" />
                <h3 className="font-semibold">AI Insights</h3>
              </div>
              <div className="space-y-3">
                <div className="bg-white/10 rounded-lg p-3">
                  <p className="text-sm font-medium">VAT filing due in 5 days</p>
                  <p className="text-white/70 text-xs mt-1">Ensure documents are ready</p>
                </div>
                <div className="bg-white/10 rounded-lg p-3">
                  <p className="text-sm font-medium">Revenue up by 12.5%</p>
                  <p className="text-white/70 text-xs mt-1">Great progress this month</p>
                </div>
                <div className="bg-white/10 rounded-lg p-3">
                  <p className="text-sm font-medium">3 new clients added</p>
                  <p className="text-white/70 text-xs mt-1">Business growth detected</p>
                </div>
              </div>
              <button className="w-full mt-4 py-2 bg-white/20 rounded-lg hover:bg-white/30 transition-colors text-sm font-medium">
                Ask AI Assistant
              </button>
            </div>
          </div>

          {/* Compliance Status */}
          <div className="bg-white rounded-lg border border-gray-200 p-6">
            <h3 className="font-semibold text-gray-900 mb-4">Compliance Status</h3>
            <div className="space-y-3">
              <div className="flex items-center justify-between">
                <span className="text-sm text-gray-600">VAT Registration</span>
                <CheckCircle size={16} className="text-green-500" />
              </div>
              <div className="flex items-center justify-between">
                <span className="text-sm text-gray-600">Trade License</span>
                <CheckCircle size={16} className="text-green-500" />
              </div>
              <div className="flex items-center justify-between">
                <span className="text-sm text-gray-600">ESR Filing</span>
                <Clock size={16} className="text-yellow-500" />
              </div>
              <div className="flex items-center justify-between">
                <span className="text-sm text-gray-600">Corporate Tax</span>
                <AlertCircle size={16} className="text-red-500" />
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Bottom Section - Additional Info */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mt-6">
        {/* Performance Metrics */}
        <div className="bg-white rounded-lg border border-gray-200 p-6">
          <h3 className="font-semibold text-gray-900 mb-4">Business Health</h3>
          <div className="space-y-4">
            <div>
              <div className="flex justify-between text-sm text-gray-600 mb-1">
                <span>Invoice Completion</span>
                <span>85%</span>
              </div>
              <div className="w-full bg-gray-200 rounded-full h-2">
                <div className="bg-green-500 h-2 rounded-full" style={{ width: '85%' }}></div>
              </div>
            </div>
            <div>
              <div className="flex justify-between text-sm text-gray-600 mb-1">
                <span>Client Satisfaction</span>
                <span>92%</span>
              </div>
              <div className="w-full bg-gray-200 rounded-full h-2">
                <div className="bg-blue-500 h-2 rounded-full" style={{ width: '92%' }}></div>
              </div>
            </div>
            <div>
              <div className="flex justify-between text-sm text-gray-600 mb-1">
                <span>Compliance Score</span>
                <span>78%</span>
              </div>
              <div className="w-full bg-gray-200 rounded-full h-2">
                <div className="bg-orange-500 h-2 rounded-full" style={{ width: '78%' }}></div>
              </div>
            </div>
          </div>
        </div>

        {/* Quick Tips */}
        <div className="bg-gradient-to-br from-[#1b2a49] to-[#2E69A4] rounded-lg text-white p-6">
          <h3 className="font-semibold mb-3">Quick Tip</h3>
          <p className="text-sm text-white/80 mb-4">
            Did you know? You can automate your VAT calculations and generate compliant invoices 
            with just a few clicks using our AI assistant.
          </p>
          <button className="flex items-center gap-2 text-sm font-medium hover:text-cyan-200 transition-colors">
            Learn More
            <ArrowUpRight size={16} />
          </button>
        </div>
      </div>
    </div>
  );
};

export default DashboardPage;